"use strict";
var mobile = false,
    desktop = true,
    constWindowScrollTop = 0,
    windowScrollTop = 0;

$(document).ready(function(){

    mobile = isMobile();
    desktop = (isMobile() || isTablet()) ? false : true;
    if (desktop) $('html').addClass('desktop');

    headerFunctions();
    inputFileFunctions();
    selectFunctions();

    var rangesliderUnit = ' %';
    $('input[type="range"]').rangeslider({
        polyfill : false,
        onInit : function() {
            this.output = $( '<div class="range-output" />' ).insertAfter( this.$range ).html( this.$element.val() + rangesliderUnit );
        },
        onSlide : function( position, value ) {
            this.output.html( value + rangesliderUnit );
        }
    });

    if (!isMobile()) {
        new SimpleBar($('.custom-scroll')[0]);
    }
    // $('.custom-scroll').each(element, new SimpleBar());

});



const headerFunctions = () => {
    let header = $('.header');

    function repeatOften() {
        setHeadeModeOnScroll();
        requestAnimationFrame(repeatOften);
    }
    requestAnimationFrame(repeatOften);

    function setHeadeModeOnScroll() {
        windowScrollTop = $(document).scrollTop();
        if (windowScrollTop > 50) {
            header.addClass('fixed-mode');
        } else {
            header.removeClass('fixed-mode');
        }
    }

    $('.menu-toggle').on('click', function (e) {
        let ths = $(this);
        let headerMenu = $('.menu-list');

        if (!ths.hasClass('active')) {
            ths.addClass('active');
            headerMenu.slideDown(300);
            disableScroll();
        } else {
            ths.removeClass('active');
            headerMenu.slideUp(300);
            enableScroll();
        }
        e.preventDefault();
    });

    $('a[data-anchor]').on('click', function (e) {
        let ths = $(this);
        let thsAttr = ths.data('anchor');
        let section = $('.main').find('[data-anchor='+thsAttr+']');

        if (section.length) {
            $('html, body').animate({
                scrollTop: section.offset().top
            }, 1000);
            e.preventDefault();
        }
    });
};

const selectFunctions = () => {
    $('.select').each(function () {

        let ths = $(this);
        let toggle = ths.find('.select__custom');
        let toggleText = toggle.find('.select__name');
        let list = ths.find('.select-list');
        let input = ths.find('.select__input');

        toggle.on('click', function () {
            if (!ths.hasClass('active')) {
                ths.addClass('active');
            } else {
                ths.removeClass('active');
            }
        });
        list.find('.selet-option').on('click', function (e) {
            let thsItem = $(this);
            let thsAttr = thsItem.data('value');
            let thsText = thsItem.html();
            input.val(thsAttr);
            toggleText.html(thsText);
            ths.removeClass('active');
            e.preventDefault();
        });
    });
    $(document).on('click', function (e) {
        if(!$(e.target).closest('.select').length) {
            $('.select').removeClass('active');
        }
    });
};

const inputFileFunctions = () => {
    var inputFile = $('.file-label input[type="file"]');
    if (inputFile.length > 0) {
        inputFile.each(function(){
            var ths = $(this);
            var mainBlock = ths.closest('.attach-file');
            var $placeholder = mainBlock.find(".uploaded__name"),
                placeholder_text = $placeholder.text();
            // var fileMaxSizeMb = 10;

            ths.on("input change blur", function(){
                if (this.files[0] && ths.val()) {
                    var file = this.files[0];
                    var filename = file.name;

                    mainBlock.removeClass('error-state');

                    // if (file.size > fileMaxSizeMb*1000000) {
                    //     $(this).val('');
                    //     mainBlock.addClass('error-state');
                    //     return false
                    // }
                    $placeholder.text(filename);
                    mainBlock.addClass('uploaded')
                } else {
                    $placeholder.text(placeholder_text)
                }
            })
        });
        $(document).on('click', '.uploaded__remove', function (e) {
            var ths = $(this);
            var mainBlock = ths.closest('.attach-file');
            var uploadName = mainBlock.find('.uploaded__name');
            var fileInput = mainBlock.find('input[type=file]');
            mainBlock.removeClass('uploaded');
            fileInput.val('');
            uploadName.text('');
            e.preventDefault();
        });
    }
}

